import React from 'react';
import { connect } from 'react-redux';

class SDKIcon extends React.Component {

    getClass(iconStyle){

        iconStyle = iconStyle.substring(iconStyle.indexOf(' ')+1);
        return this.props.preference_style + ' ' + iconStyle;
    }

    render() {
        const {iconStyle} = this.props;
        let iconClass = this.getClass(iconStyle);

        return (
            <i className={iconClass} onClick={this.props.onClick}></i>
        )
    }
}

const mapStateToProps = state => {
    return {      
      'preference_style': state.general.preference_style
    };
  };

  export default connect(mapStateToProps)(SDKIcon)
  